.App {
  text-align: center;
}

html {
  background-color: #32373a
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.app-page {
  width: 100%;
  height: 100%;
}

.graph-body {
  margin: auto;
  width: 80%;
}

.btn {
  border: 2px solid black;
  background-color: #32373a;
  color: black;
  padding: 8px 10px;
  margin: 2px;
  margin-bottom: 10px;
  font-size: 16px;
  cursor: pointer;
  border-radius: 5px;
  min-width: 100px;
}

.header {
  padding: 1rem;
}
/* Green */
.success {
  border-color: #4CAF50;
  color: green;
}

.success:hover, .success-selected {
  background-color: #4CAF50;
  color: white;
}

/* Blue */
.info {
  border-color: #2196F3;
  color: dodgerblue;
}

.info:hover, .info-selected {
  background: #2196F3;
  color: white;
}

/* Orange */
.warning {
  border-color: #ff9800;
  color: orange;
}

.warning:hover, .warning-selected {
  background: #ff9800;
  color: white;
}

/* Red */
.danger {
  border-color: #f44336;
  color: red;
}

.danger:hover, .danger-selected {
  background: #f44336;
  color: white;
}

/* Gray */
.default {
  border-color: #e7e7e7;
  color: black;
}

.default:hover {
  background: #e7e7e7;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
